.homeTitle {
  margin-top: 60px;
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 44px;
  @media screen and (min-width: 768px) {
    font-size: 68px;
    line-height: 100px;
  }
  @media screen and (min-width: 1280px) {
    width: 588px;
  }
}
.homeSection {
  display: block;
  position: absolute;
  width: 100%;
  @media screen and (min-width: 1280px) {
    top: 85px;
  }
}
.homeImg {
  display: block;

  width: 100%;
}
